/* harmony default export */ __webpack_exports__["default"] = ("<p class=\"title3 text-center\" id=\"section-1\">Data Science</p>\n\n<div class=\"row\">\n    <div class=\"col-md-12\">\n            <img src=\"../../../../assets/cyspace-images/data-analytics/Data science.jpg\" class=\"img-fluid\" alt=\"Responsive image\" width=\"100%\">\n    </div>\n    <div class=\"col-md-12\">\n <p class=\"homep\">\n    CySpace is adept at leveraging experienced data scientists and methodologies to use advanced models and suitable algorithms that expertly segregate the signals from the noise, identify the patterns in data and provide predictive insights, and analysis.\n</p>\n<p class=\"homep\">\n    CySpace imparts Artificial Intelligence (AI) in business methods—giving an edge to our clients to perform in their verticals in different ways and do different things. Our unique approach to utilizing AI, automation and analytical stats at the nucleus of the entire enterprise to create smooth and adaptive processes, enable efficient decision making and give a boost to businesses to bring in new opportunities. Analytics, Insights, and Intelligence: We provide generic or customized services involving Big Data, predictive, and prescriptive analytics.\n\n</p>\n<p class=\"homep\">\n    Trust CySpaceAI Technology Pvt Ltd as your data management partner to comprehensively address your evolving data management requirements. Make decisions more efficiently with a highly visual representation of data.\n\n</p>\n  </div>\n\n\n\n\n\n  <p class=\"title3 text-center\" id=\"section-2\">CySpace Methodologies</p>\n\n<div class=\"row\">\n    <div class=\"col-md-12\">\n            <img src=\"../../../../assets/cyspace-images/data-analytics/\" class=\"img-fluid\" alt=\"Responsive image\" width=\"100%\">\n    </div>\n    <div class=\"col-md-12\">\n<p class=\"homep\">\n    At CySpace Global™, we collaborate with our customers at every step to answer the unasked questions. Our expertise in domain, technology, and execution, empowers us to transform insights into fore-sights. \n</p>\n<p class=\"homep\">\n    At CySpace we believe that an Enterprise Data Management Strategy should offer a significant return on investment in building and executing a holistic Enterprise Data Strategy roadmap. Also, our Enterprise Analytics Services help clients develop a more agile, feasible program to obtain real-time insights and achieve high potential growth. Our expert team offers Business Intelligence, Information/Application Management, and Data Mining for all kinds of industry variants. We help you customize and implement the latest data management strategies and technologies to build the best, most comprehensive processes and platforms.  \n</p>\n</div>\n  </div>\n\n\n\n\n  <p class=\"title3 text-center\" id=\"section-3\">Data and analytics strategy</p>\n<div class=\"row\">\n    <div class=\"col-md-12\">\n            <img src=\"../../../../assets/cyspace-images/data-analytics/Data Analytics.jpg\" class=\"img-fluid\" alt=\"Responsive image\" width=\"100%\">\n    </div>\n    <div class=\"col-md-12\">\n<p class=\"homep\">\n    CySpace understands that having a crystal-clear data strategy is imperative, given the sheer amount of data that is generated these days. We develop a smart strategy that focuses on the data that is needed to achieve your goals. We'll map your analytics initiatives to quantifiable business outcomes with a data-driven approach.\n</p>\n</div>\n</div>\n\n<p class=\"title3 text-center\" id=\"section-4\">Data discovery and augmentation</p>\n<div class=\"row\">\n    <div class=\"col-md-12\">\n            <img src=\"../../../../assets/cyspace-images/data-analytics/Data discovery and Augmentation.jpg\" class=\"img-fluid\" alt=\"Responsive image\" width=\"100%\">\n    </div>\n    <div class=\"col-md-12\">\n<p class=\"homep\">\n    CySpace offers services to significantly reduce the time and complexity of devising valuable insights from data sets, especially ones with no proper structure. We use cognitive services to scale more efficiently than manual processes, processing up-to-the-millisecond data on the go to get instant data. We bring you 360-degree customer views by augmenting your assets with third-party data and predictive analytics.\n</p>\n</div>\n</div>\n\n<p class=\"title3 text-center\" id=\"section-5\"> Data management </p>\n<div class=\"row\">\n    <div class=\"col-md-12\">\n            <img src=\"../../../../assets/cyspace-images/data-analytics/Data-Management.jpg\" class=\"img-fluid\" alt=\"Responsive image\" width=\"100%\">\n    </div>\n    <div class=\"col-md-12\">\n<p class=\"homep\">\n    In addition to data management and analytics, we assist with cybersecurity, governance and compliance solutions.\nOur Data Management services include\n</p>\n<ul class=\"self1\">\n<p class=\"text-center title2\">Video Analytics</p>\n<li>\n    <p class=\"homep\">Video Analytics is the capability of automatically analyzing video to detect and determine temporal and spatial events. Video Content Analysis is computerized video footage analysis that uses algorithms to differentiate between object types and identify certain behavior or action in real-time, providing alerts and insights to users.\n    </p>\n    <p class=\"homep\">\n        This technical capability is used in a wide range of domains including entertainment, health-care, retail, automotive, transport, home automation, flame, and smoke detection, safety and security. The algorithms can be implemented as software on general-purpose machines, or as hardware in specialized video processing units. Video analytics include below comprehensive solutions.\n   </p>\n   <ul class=\"self\">\n       <li>Security for Smart cities</li>\n       <li>Home Automation with IoT</li>\n       <li>Crowd Management & People Count\n        Suspect search\n        </li>\n        <li>Face Recognition\n        </li>\n        <li>Leakage Detection\n        </li>\n        <li>Emotional Analysis</li>\n   </ul>\n</li>\n</ul>\n\n\n\n\n<ul class=\"self1\">\n    <p class=\"text-center title2\"> Predictive analytics using MI services</p>\n    <li>\n        <p class=\"homep\">\n            Predictive analytics is the use of data, algorithms, and machine learning techniques to assign ‘scores’ to various user segments based on historical data. Its goal is to assess a likelihood of what will happen in the future such as making a purchase, predicting churn or influencing a customer or user behavior so that a specific action can be taken.\n\n        </p>\n        <p class=\"homep\">\n            Business thrives on continuous improvement we provide predictive analytics as a service that continuously evolves with your needs, here at CySpace Global™™ we specialize in converting data of all sizes into actionable insights for decision-makers in some of the world's largest companies.\n        </p>\n        <p>Our use cases include the following services.</p>\n       <ul class=\"self\">\n           <li>Multi-Channel Campaign Automation</li>\n           <li>Self-Optimizing Personalization</li>\n           <li>Recommendations for eCommerce cross-selling and upselling</li>\n            <li>Predictive maintenance and quality fulfillment with IoT monitoring</li>\n            <li>Prediction of features for product development and Customer Modeling</li>\n            <li>Inventory forecasting and pricing strategy and real-time hyper targeting</li>\n            <li>Calculation of risk</li>\n       </ul>\n    </li>\n    </ul>\n\n\n\n    <ul class=\"self1\">\n        <p class=\"text-center title2\">Spend Analytics</p>\n        <li>\n            <p class=\"homep\">\n                Spend Analytics helps gain visibility and dive deep into spend data, track procurement performance metrics, supply base, and provides actionable insights to identify opportunities for consolidation of spend and to reduce procurement costs, our use cases helps customer to\n            </p>\n            <ul class=\"self\">\n                <li>Rationalize the supply base and Improve visibility and compliance</li>\n                <li>Reduce maverick spend</li>\n                <li>Reduce Spend management costs</li>\n                 <li>Capitalize on opportunities in price discovery, categorization and payment term optimization</li>\n                 <li>Deliver unit cost savings from unmanaged spend</li>\n                 <li>Harmonize pricing across buying centers</li>\n            </ul>\n            <p>When procurement teams work closely with other departments and suppliers, they can deliver value through:\n            </p>\n           <ul class=\"self\">\n               <li>Efficient sourcing - with quicker cycle times and a closer match to the required goods or services</li>\n               <li>Optimizing the supply base lower-risk vendors that deliver quality products and services</li>\n               <li>Innovation - working with the supply chain to deliver new products or services </li>\n                <li>Capital improvements and cost reductions - when faced with unforeseen challenges such as cash-flow issues, labor shortages, and supply-chain disruption</li>\n           </ul>\n        </li>\n        </ul>\n</div>\n</div>\n<p class=\"title3 text-center\" id=\"section-6\">Industrialized solutions</p>\n<div class=\"row\">\n    <div class=\"col-md-12\">\n            <img src=\"../../../../assets/cyspace-images/data-analytics/Industrialized solutions.jpg\" class=\"img-fluid\" alt=\"Responsive image\" width=\"100%\">\n    </div>\n    <div class=\"col-md-12\">\n <p class=\"homep\">\n    We offer generic solutions for common challenges as well as customized solutions for more needs.\n</p>\n</div>\n  </div>");

